'use client'
import {useState} from 'react';

interface TableProps {
  columns: string[];
  selectableColumns: string[];
  options: string[][];
}

const Table: React.FC<TableProps> = ({ columns, selectableColumns, options }) => {
  const [data, setData] = useState([
    { id: 1, textValues: ["Item 1", "Desc 1"], selectValues: ["Option 1"] },
    { id: 2, textValues: ["Item 2", "Desc 2"], selectValues: ["Option 2"] },
  ]);
  const [selectedRows, setSelectedRows] = useState<number[]>([]);

  const handleTextChange = (rowIndex: number, colIndex: number, value: string) => {
    const newData = [...data];
    newData[rowIndex].textValues[colIndex] = value;
    setData(newData);
  };

  const handleSelectChange = (rowIndex: number, colIndex: number, value: string) => {
    const newData = [...data];
    newData[rowIndex].selectValues[colIndex] = value;
    setData(newData);
  };

  const handleCheckboxChange = (id: number) => {
    setSelectedRows((prev) =>
      prev.includes(id) ? prev.filter((rowId) => rowId !== id) : [...prev, id]
    );
  };

  return (
    <div className="overflow-x-auto">
      <table className="min-w-full bg-white border border-gray-300">
        <thead>
          <tr className="bg-gray-200">
            <th className="p-2 border">Select</th>
            {columns.map((col, index) => (
              <th key={index} className="p-2 border">{col}</th>
            ))}
            {selectableColumns.map((col, index) => (
              <th key={index} className="p-2 border">{col}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.map((row, rowIndex) => (
            <tr key={row.id} className="border">
              <td className="p-2 border text-center">
                <input
                  type="checkbox"
                  checked={selectedRows.includes(row.id)}
                  onChange={() => handleCheckboxChange(row.id)}
                />
              </td>
              {row.textValues.map((value, colIndex) => (
                <td key={colIndex} className="p-2 border">
                  <input
                    type="text"
                    value={value}
                    onChange={(e) => handleTextChange(rowIndex, colIndex, e.target.value)}
                    className="w-full border rounded p-1"
                  />
                </td>
              ))}
              {row.selectValues.map((value, colIndex) => (
                <td key={colIndex} className="p-2 border">
                  <select
                    value={value}
                    onChange={(e) => handleSelectChange(rowIndex, colIndex, e.target.value)}
                    className="w-full border rounded p-1"
                  >
                    {options[colIndex].map((opt, optIndex) => (
                      <option key={optIndex} value={opt}>{opt}</option>
                    ))}
                  </select>
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default Table;